:root {
  --mq-size-sm: 576px;
  --mq-size-md: 768px;
  --mq-size-lg: 992px;
  --mq-size-xl: 1200px;
  --theme-color-base: #564A8A;
  --theme-color-secondary: #BFBFBF;
  --theme-secondary-grey:#D4D4D4;
  --theme-secondary-grey2:#747474;
  --btn-color-base: #FED305;
  --text-color-base: #272727;
  --text-color-secondary: #fff;
  --text-color-three: #211381;
}

.btn {
  position: relative;
  text-align: center;
  padding: 16px 27px;
  background: var(--btn-color-base);
  border: none;
  border-radius: 5px;
  text-decoration: none;
  font-family: 'YS_Text', sans-serif;
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  color: var(--text-color-base);
}

.btn:hover {
  color: var(--text-color-secondary);
  box-shadow: 4px 5px 4px rgba(255, 255, 255, 0.25);
}

.btn:active {
  color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 0px 10px 6px rgba(255, 255, 255, 0.25) inset;
}

.menu__btn {
  width: 17px;
  height: 17px;
  position: relative;
  z-index: 6;
  cursor: pointer;
}

.menu__btn--line {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  margin-bottom: 2px;
  background-color: var(--text-color-secondary);
  transition-duration: 0.1s;
}

.menu__btn .first {
  top: 0%;
}

.menu__btn .second {
  top: 50%;
  transition-duration: 0.5s;
  transform-origin: 50% 50%;
}

.menu__btn .second-ex {
  top: 50%;
  transition-duration: 0.5s;
  transform-origin: 50% 50%;
}

.menu__btn .third {
  top: 100%;
}

.is-active {
  position: fixed;
  right: 3%;
}

.is-active .first {
  opacity: 0;
}

.is-active .second {
  transform: skewY(45deg);
}

.is-active .second-ex {
  transform: skewY(-45deg);
}

.is-active .third {
  opacity: 0;
}

